body, h1, h2, h3, h4, h5, h6, p, div {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgba(0, 0, 0, 0.07);
  max-width: 1920px;
  margin: auto;
  background-image: url("../src/images/testBackground.png");
  background-repeat: no-repeat;
  background-size: 100% 10%;
}

main {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 12vh;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.flexColumnBetween {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.flexBetween {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.link {
  text-decoration: none;
  color: black;
}

@media screen and (max-width: 650px) {
  main {
    margin-top: 6vh;
  }
  body {
    background-size: 100% 6%;
  }
}